@import url(https://fonts.googleapis.com/css?family=Roboto:100,300,400,700&subset=latin-ext);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@-webkit-keyframes loading-small {
    0% {
        height: 0%
    }

    50% {
        height: 70%
    }

    100% {
        height: 0%
    }
}
@keyframes loading-small {
    0% {
        height: 0%
    }

    50% {
        height: 70%
    }

    100% {
        height: 0%
    }
}
.welcome-page section {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-direction: column;
    height: 90vh;
    margin-top: 10vh;
}

.intro {

    background-color: rgb(29, 117, 168);

}

.technology {
    background-color: rgb(255, 255, 255);
    color: rgb(29, 27, 27)
}

.section-tittle {
    margin: 20px 0;
    font-size: 2.6rem;
    font-weight: 300;
}

.section-describe {
    font-weight: 100;
    width: 41vw;
    font-size: 2.2rem;
    text-align: justify;
    -moz-text-align-last: center;
         text-align-last: center;
}

.tech-wrap {
    display: flex;
    width: 100%;
    justify-content: space-evenly;
}

.tech-card {
    border: 1px solid black;
    width: 30%;
    height: 60vh;
    border-radius: 3px;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 15px;
    font-size: 1.6rem;
    font-weight: 300;

}

.tech-card .card-desc {
    text-align: justify;
    -moz-text-align-last: center;
         text-align-last: center;
    height: 50%;
}

.card-icon-wrap {
    height: 40%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 3.6rem;
}

.tech-card:nth-child(2) .card-icon-wrap,
.tech-card:nth-child(3) .card-icon-wrap {
    font-size: 3.8rem;
}
.nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 8vh;
    background-color: rgb(29, 117, 168);
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 2;
    box-shadow: 0px 10px 40px 5px rgba(38, 127, 179, 0.137);
}

.nav.nav1 {
    height: 10vh;
}

.buttons {
    display: flex
}

.btn {
    border: 1px solid rgb(197, 197, 197);
    height: 30px;
    width: 100px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 20px;
    border-radius: 2px;
    font-size: 1.5rem;

}

.logo,
.btn {
    font-weight: 300;


    cursor: pointer;
}

.logo {
    font-size: 2.2rem;
}
.loading_small--bg {
    width: 100vw;
    height: 100vh;
    background-color: rgba(255, 255, 255, 0.63);
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
}

.loading_bars--wrapper {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    width: 5vw;
    height: 3vw;
}

.loading_bar {
    position: relative;
    top: 0;
    left: 0;
    height: 0%;
    width: 5px;
    background-color: #1d75a8;
    -webkit-animation-name: loading-small;
            animation-name: loading-small;
    -webkit-animation-duration: 1000ms;
            animation-duration: 1000ms;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-fill-mode: both;
            animation-fill-mode: both;
    -webkit-animation-timing-function: ease;
            animation-timing-function: ease;
}

.loading_bar--end {
    background-color: #1d75a8;
    left: 0;
    position: absolute;
    border-radius: 50%;
    width: 5px;
    height: 5px;

}

.loading_bar--end:nth-child(1) {
    top: 0;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%)
}

.loading_bar--end:nth-child(2) {
    bottom: 0;
    -webkit-transform: translateY(50%);
            transform: translateY(50%)
}

/* loading bar set animaion */
.loading_bar:nth-child(1) {
    -webkit-animation-delay: 0ms;
            animation-delay: 0ms
}

.loading_bar:nth-child(2) {
    -webkit-animation-delay: 100ms;
            animation-delay: 100ms
}

.loading_bar:nth-child(3) {
    -webkit-animation-delay: 200ms;
            animation-delay: 200ms
}

.loading_bar:nth-child(4) {
    -webkit-animation-delay: 300ms;
            animation-delay: 300ms
}

.loading_bar:nth-child(5) {
    -webkit-animation-delay: 400ms;
            animation-delay: 400ms
}
.log-form {
    display: flex;
    align-items: flex-start;
    justify-content: center;
    flex-direction: column;

}

.log-form input {
    height: 40px;
    width: 30vw;
    border-radius: 3px;
    border: 1px solid rgb(189, 189, 189);
    font-size: 1.6rem;
    color: rgb(104, 104, 104);
    margin-bottom: 20px;
    background-color: rgb(240, 240, 240);
    padding: 5px;
}

.login-alert {
    color: red;
    font-weight: 300;
}

#rememberMe {
    height: 18px;
    width: 18px;
}

.log-form label {
    font-size: 1.8rem;
    font-weight: 300;
    color: rgb(82, 82, 82);
}
.reg-log-wrapper {
    height: 88vh;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    color: rgb(36, 34, 34)
}


.reg-sub {
    width: 100%;
    height: 40px;
    border: none;
    border-radius: 3px;
    background-color: rgb(219, 219, 219);
    color: rgb(82, 82, 82);
    font-size: 1.8rem;
    cursor: pointer;
    -webkit-transition: 100ms;
    transition: 100ms;
}

.reg-sub:hover {
    background-color: rgb(212, 212, 212);
}

.reg-sub:active {
    -webkit-transform: scale(0.99);
            transform: scale(0.99)
}

.postreg-link {
    font-size: 1.5rem;
    -webkit-transition: 200ms;
    transition: 200ms
}

.postreg-link:hover {
    -webkit-transform: scale(1.01);
            transform: scale(1.01)
}
footer {
    background-color: rgb(29, 117, 168);
    height: 12vh;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: column;
    font-size: 1.5rem;

}

footer p:nth-child(1) {
    margin-top: 10px;
}



footer a {
    font-size: 2.2rem;
    color: white;
}
.main-wraper {
    height: 88vh;
    display: flex;
}
.main__window {
    flex-basis: 80%;
    height: 100%;

}

.main__content-wrapper {
    width: 100%;
    height: 100%;
    color: black;
}

.main__init-wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
}

.main__init-wrapper p {
    font-size: 3rem;
}

.main__content--list {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.main__active-list--info {
    width: 100%;
    flex-basis: 10%;
    display: flex;
    justify-content: center;
    padding: 20px 0 0 0;
    background-color: rgb(255, 194, 64);
}

.app__select {
    border: none;
    border-bottom: 1px solid white;
    outline: none;
}

.listView__form--stages select {
    background-color: rgb(255, 194, 64);

}

.task-stage-wrap {
    flex-grow: 1;
    display: flex;
    justify-content: center;
    overflow: hidden;
    margin: 0 2%;

}
.tasks__view {
    display: flex;
    flex-direction: column;
    width: 80%;
    overflow-y: scroll;
    scrollbar-width: none;

}

.tasks__view::-webkit-scrollbar {
    width: 0;
}

.task--wrapp {
    margin: 10px 0;
    background-color: rgb(255, 255, 255);
    width: 100%;
    min-height: 60px;
    border: 1px solid rgb(97, 97, 97);
    border-radius: 15px;
    display: flex;
    align-items: center;
    position: relative;
    top: 0;
    left: 0;
}


.task-name {
    height: 30px;
    margin-top: 5px;
}

.task__input--desc,
.task-name {
    width: 100%;
    outline: none;
    background-color: transparent;
    border: none;
}

.task__body {
    margin: 0 20px;
    display: flex;
}

.task__input--desc {}

.task__head {
    display: flex;
    justify-content: space-between;
}

.task__options {
    display: flex;
    margin: 0 50px;
    align-items: center;
}

.task__option {
    border-radius: 100%;
    width: 15px;
    height: 15px;
    background-color: red;
    margin: 0 10px;
}

.task__option:nth-child(2) {
    background-color: rgb(0, 204, 255);
}

.task__option:nth-child(3) {
    background-color: rgb(187, 110, 250);
}

.sortable-ghost {
    opacity: 0.4;
    background-color: rgb(131, 205, 255);
    border: none;
}

.sortable-drag {
    background-color: rgba(255, 255, 255, 0.445);
}

.my-handle {
    cursor: pointer;
    justify-self: flex-start;
    font-size: 1.8rem;
    color: gray;
    margin: 0 10px;
}
.main__navigaction {
    flex-basis: 20%;
    border-right: 1px solid rgb(226, 226, 226);
    box-shadow: 7px 0px 28px -26px rgba(13, 17, 36, 0.75);
    -webkit-transition: 150ms;
    transition: 150ms;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: black;
}

.main__navigaction:hover {
    box-shadow: 9px 0px 28px -22px rgba(0, 0, 0, 0.493);
}

.navigaction__user--wrapper {
    height: 10%;
    width: 90%;
    margin-bottom: 20px;
}

.navigaction__user {
    width: 100%;
    height: 100%;
    border-bottom: 1px solid rgba(236, 236, 236, 0.767);
    box-shadow: 0px 9px 21px -24px rgba(0, 0, 0, 0.75);
    -webkit-transition: 100ms;
    transition: 100ms;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 10px;
}

.navigation__thumbnail {
    height: 100%;
    margin-right: 10%;
}

.navigaction__user:hover {
    border-bottom: 1px solid rgb(236, 236, 236);

    box-shadow: 0px 12px 21px -24px rgba(0, 0, 0, 0.75);
}

.navigaction__list {
    display: flex;
    flex-direction: column;

}

.navigation__item {
    color: black;
    display: flex;
    justify-content: center;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-decoration: none;
  list-style-type: none;
}

:root {
  font-size: 10px;
}

body {
  font-family: 'Roboto', sans-serif;

  line-height: 1.7;
  color: whitesmoke;
  scroll-behavior: smooth;

}

.app-wrapper {
  position: relative;
  top: 0;
  left: 0;
}

/* pading for every main element*/
.nav,
.welcome-page section {
  padding: 0 40px;
}

.save-btn {
  border: none;
  font-size: 1.5rem;
  text-transform: uppercase;
  font-weight: 300;
  padding: 8px;
  background-color: rgb(29, 117, 168);
  border-radius: 4px;
  color: white;
  letter-spacing: 1px;
  cursor: pointer;
}

.save-btn:hover {
  box-shadow: 0px 10px 20px 0px rgb(235, 235, 235);
  -webkit-transform: scale(1.01);
          transform: scale(1.01)
}
